body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, form, fieldset, input, p, blockquote, th, td {
    margin: 0;
    padding: 0;
}

body {
    font-size: 12px;
    color: #666;
    font-family: "Helvetica Neue", Helvetica, Arial, "PingFang SC", "Hiragino Sans GB", "HeitiSC", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;
    background: #fff;
}

fieldset, img {
    border: 0;
}

ol, ul {
    list-style: none;
}

h1, h2, h3, h4, h5, h6 {
    font-size: 100%;
}

em {
    font-style: normal;
}

input, button, select, textarea {
    outline: none;
}

textarea {
    resize: none;
}

p {
    text-align:justify;
    text-justify:distribute;
}

a {
    color: #666;
    text-decoration:none;
}

a:hover {
    color: #f60;
    text-decoration:none;
}

.flex {
  display: -webkit-box; /* 老版本 - 适配iOS 6-, Safari 3.1-6 */ /* 新版本 - 适配Chrome */
  display: -ms-flexbox;
  display: flex;
}

/*弹窗 提示*/

._alert {
    z-index: 999;
    -webkit-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
    position: fixed;
    top: 0;
    text-align: center;
    display: table;
    width: 100%;
    margin: 4rem auto 0;
    pointer-events: none;
}

._alert > span {
    display: inline-block;
    max-width: 5.5rem;
    text-align: left;
    padding: 0.19rem 0.4rem;
    border-radius: 0.08rem;
    color: rgba(228, 192, 155, 1);
    font-size: 0.32rem;
    background: rgba(0, 0, 0, 0.9);
    /*background: #181840;*/
}

::-webkit-input-placeholder { /* WebKit browsers */
    color:    rgba(187,187,187,1);
}

:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
    color:    rgba(187,187,187,1);
}

::-moz-placeholder { /* Mozilla Firefox 19+ */
    color:    rgba(187,187,187,1);
}

:-ms-input-placeholder { /* Internet Explorer 10+ */
    color:    rgba(187,187,187,1);
}